.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  background-color: #497e7d;
  position: absolute;
}

.c-wrapper {
  padding: 50px;
  display: flex;
}

.c-left {
  flex: 1;
}

.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.c-title {
  font-size: 60px;
  width: 80%;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-weight: 200;
}

form {
  margin-top: 20px;
}

input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

textarea {
  width: 100%;
}

button {
  border: none;
  padding: 15px;
  background-color: #497e7d;
  color: white;
  font-weight: 500;
  cursor: pointer;
}
@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
  }

  .c-title {
    display: none;
  }

  .c-info-item {
    display: flex;
    align-items: center;
    margin: 20px 0px;
    font-weight: 300;
    font-size: 14px;
  }

  .c-icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  form {
    margin-top: 10px;
  }

  .c-wrapper {
    padding: 30px;
    display: flex;
  }
}
